import React, { useState, useEffect, useRef } from "react";
import { Helmet } from 'react-helmet';
import SportsIITGNVideo from "../assets/video/IIT Gandhinagar.mp4"
import ImageCarousel from "../components/ImageCarousel";
import InstagramContainer from "../components/InstagramContainer";
const Home = () => {
  const videoEl = useRef(null);
  const attemptPlay = () => {
    videoEl &&
      videoEl.current &&
      videoEl.current.play().catch(error => {
        console.error("Error attempting to play", error);
      });
  };

  useEffect(() => {
    attemptPlay();
  }, []);

  const [imageUrls] = useState([
    require("../assets/images/banner-1.jpg"),
    require("../assets/images/banner-2.jpg"),
    require("../assets/images/banner-3.jpg"),
  ]);
  const [currentIndex, setCurrentIndex] = useState(0);
  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentIndex(currentIndex => (currentIndex + 1) % imageUrls.length);
    }, 3000);
    return () => clearInterval(intervalId);
  }, [imageUrls]);

  const backgroundImage = imageUrls[currentIndex];



  return (
    <div>
      <Helmet>
        <title>Home | Sports at IITGN </title>
      </Helmet>
      <div className='main-container'>
        <header id="head" style={{
          backgroundImage:`url(${backgroundImage})`,
          transition: "1s ease",
          backgroundSize: "cover",
          backgroundAttachment: "fixed",
          backgroundBlendMode: "overlay",
          flexDirection: "column",
          backgroundRepeat:"space",
        }}>
          <div className="banner-content">
            <h1>Sports at IIT Gandhinagar</h1>
          </div>
        </header>


        <div className="section">

          <div className="about-section">
            <p>
              At IIT Gandhinagar, we have a vibrant culture of sports. We believe that a person's sense of integrity, dedication, and perseverance is tested on the sports field more than anywhere else. IITGN has always supported sports and fitness as it helps maintain a healthy mind and body. Students must be allowed to choose the sport of their liking and capability and must be given an equal chance to excel in it. Keeping this in mind, ample facilities have been provided to the students for practising and attaining professionalism in their respective games.
            </p>
          </div>
        </div>








        <div className="recent-events-section">
          <div className="section-heading">
            <h1>Recent Events</h1>

          </div>

          <ImageCarousel />
        </div>

        <div className="section">
          <div className="instagramContainer">
            <div className="section-heading">
              <h1>Sports@IITGN on Instagram</h1>
            </div>

            <InstagramContainer />
          </div>



        </div>

        <div className="home-video">
          <video
            style={{ width: "100vw", margin: "0 auto", height: "50vh", maxHeight: "500px", overflow: "hidden", justifyContent: "center", backgroundBlendMode: "overlay", background: "black", objectFit: "cover" }}
            playsInline
            loop
            muted
            controls={false}
            alt="All the devices"
            src={SportsIITGNVideo}
            ref={videoEl}
          />

        </div>


      </div>

    </div>

  )
}

export default Home