import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "./Navbar.css";
import Logo from "../assets/images/logo.png"

const Navbar = () => {
    const [click, setClick] = useState(false);
    const handleClick = () => {
        window.scrollTo(0, 0);
        setClick(!click);
    };

    return (
        
            <div className="navbar">
                <div className="nav-container">
                    <div className="nav-phone">
                        <div className="nav-logo">
                            <NavLink to="/" className="logo-image" onClick={() => window.scrollTo(0, 0)}>
                                <img src={Logo} alt="logo" />
                            </NavLink>
                        </div>
                        <div className="nav-icon" onClick={handleClick}>
                            <i className={click ? "fas fa-times" : "fas fa-bars"}></i>
                        </div>

                    </div>


                    <ul className={click ? "nav-menu nav-menu-active" : "nav-menu"}>
                        <li className="nav-item">
                            <NavLink
                                to="/"
                                className={({ isActive }) => (isActive ? 'active-nav' : 'nav-links')}
                                onClick={handleClick}
                            >
                                Home
                            </NavLink>
                        </li>

                        <div className="nav-item">
                            <li className="">
                                <button className="nav-dropbtn">
                                    <NavLink
                                        to="/sports"
                                        className={({ isActive }) => (isActive ? 'active-nav' : 'nav-links')}
                                        onClick={handleClick}

                                    >
                                        Sports <i className="fa fa-angle-down" aria-hidden="true"></i>
                                    </NavLink>
                                </button>

                                <div className="dropdown-content">
                                    <NavLink
                                        to="/sports"
                                        className={({ isActive }) => (isActive ? 'active-drop' : 'drop-nav-links')}
                                        onClick={handleClick}

                                    >
                                        Sports
                                    </NavLink>
                                    <NavLink
                                        to="/events"
                                        className={({ isActive }) => (isActive ? 'active-drop' : 'drop-nav-links')}
                                        onClick={handleClick}

                                    >
                                        Events
                                    </NavLink>
                                </div>

                            </li>
                        </div>

                        <div className="nav-item">
                            <li className="">
                                <button className="nav-dropbtn">
                                    <NavLink
                                        to="/events"
                                        className={({ isActive }) => (isActive ? 'active-nav' : 'nav-links')}
                                        onClick={handleClick}

                                    >
                                        Events <i className="fa fa-angle-down" aria-hidden="true"></i>
                                    </NavLink>
                                </button>

                                <div className="dropdown-content">
                                    <NavLink
                                        to="/events"
                                        className={({ isActive }) => (isActive ? 'active-drop' : 'drop-nav-links')}
                                        onClick={handleClick}

                                    >
                                        Events
                                    </NavLink>
                                    <NavLink
                                        to="/events/events-calendar"
                                        className={({ isActive }) => (isActive ? 'active-drop' : 'drop-nav-links')}
                                        onClick={handleClick}

                                    >
                                       Events Calendar
                                    </NavLink>
                                </div>

                            </li>
                        </div>

                        





                        <li className="nav-item">
                            <NavLink
                                to="/team"
                                className={({ isActive }) => (isActive ? 'active-nav' : 'nav-links')}
                                onClick={handleClick}
                            >
                               People
                            </NavLink>
                        </li>


                        <li className="nav-item">
                            <NavLink
                                to="/contact"
                                className={({ isActive }) => (isActive ? 'active-nav' : 'nav-links')}
                                onClick={handleClick}
                            >
                                Contact Us
                            </NavLink>
                        </li>
                        <li className="nav-item">
                        </li>
                    </ul>

                </div>

            </div>
       
    )
}

export default Navbar
